* {
    margin: 0;
    padding: 0;
}

.todoWindow {
    color: white;
    font-family: Roboto Mono,monospace;
    background-color: rgb(0, 1, 60);
    width: 80%;
    height: 80%;
    position: relative;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    display: flex;
    align-content: center;
    justify-content: flex-start;
    align-items: center;
    flex-direction: column;
}

.todoTitle {
    height: 100px;
    display: flex;
    align-content: center;
    justify-content: center;
    align-items: center;
}

.todoTitle h1 {
    font-size: 70px;
}

.todoForm {
    display:flex;
    align-content: center;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.todoForm form {
    display: flex;
    align-content: center;
    justify-content: center;
    align-items: center;
}

.todoForm form input {
    border: none;
    padding: 10px;
    width: 300px;
}

.todoForm form button {
    padding: 10px;
    border: none;
    /* padding: 5px; */
    background-color: rgb(34, 34, 34);
    color: white;
    font-size: 30px;
    font-weight: bolder;
}


.taskWindow {
    border: 4px solid rgb(162, 207, 255);
    margin-top: 10px;
    display: flex;
    justify-content: space-between;
    align-content: center;
    align-items: center;
    padding: 10px;
}

.taskWindow button {
    border: none;
    color: white;
    background-color: rgb(206, 2, 2);
    padding: 10px;
}

.clear {
    margin: 10px 0;
    padding: 10px;
    border: none;
    color: white;
    background-color: rgb(206, 2, 2);
}


